<template>
  <div>
        <div v-if="questionPage<16">

      <div v-for="question in questionList" :key="question">
        <div v-if="questionPage===question.id+1">
          <h2>{{ questionPage }}/15</h2>
      <div class="qbox" @click="$emit('questionAdd',1); nextQuestion()">{{ question.a }}</div>
      <div class="qbox" @click="$emit('questionAdd',0); nextQuestion()">{{ question.b }}</div>
        </div>
      </div>
    </div>
    <button class="main-button" v-if="questionPage==16" @click="$emit('changePage',2)">í…ŒìŠ¤íŠ¸ ê²°ê³¼ ë³´ëŸ¬ê°€ê¸°</button>

  </div>
</template>

<script>
export default {
    name: 'TestComponent',
    props:{
    page:Number,
    question:Array,
    questionList:Object,
  },
  data(){
    return{
      questionPage:1,
    }
  },
  methods:{
    nextQuestion(){
      if(this.questionPage<16){
        this.questionPage+=1;
      }
    },
  },
}
</script>

<style>
.qbox{
  box-sizing: border-box;
  font-family: "consolas";
  width: 100%;
  max-width: 350px;
  margin: auto;
  margin-bottom: 20px;
  position: relative;
  border: 2px solid #92c8ff;
  border-radius: 10px;
  cursor: pointer;
}
